
import React from "react";
// import Githubcalendar from "react-github-calendar";
import { ThemeContext } from "../../Context/theme";
// import { Statistics } from "./Statistics";

export const GithubStats = () => {
  const [{ themename }] = React.useContext(ThemeContext);
  //
  const [src, setSrc] = React.useState(
    themename === "light"
      ? "https://github-readme-streak-stats.herokuapp.com?user=vivekbtu&theme=tokyonight&hide_border=true&border_radius=20&background=FCFCFC&stroke=677F84&ring=2978B5&fire=677F84&currStreakNum=183C44&sideNums=183C44&currStreakLabel=405E65&sideLabels=405E65&dates=677F84"
      : "https://github-readme-streak-stats.herokuapp.com?user=vivekbtu&theme=tokyonight&hide_border=true&border_radius=20&background=23283E&stroke=90A0D9&ring=90A0D9&fire=CDCDFF&currStreakNum=CDCDFF&sideNums=CDCDFF&currStreakLabel=90A0D9&sideLabels=90A0D9&dates=CDCDFF"
  );

  React.useEffect(() => {
    if (themename === "dark") {
      setSrc(
        "https://github-readme-streak-stats.herokuapp.com?user=vivekbtu&theme=tokyonight&hide_border=true&border_radius=20&background=23283E&stroke=90A0D9&ring=90A0D9&fire=CDCDFF&currStreakNum=CDCDFF&sideNums=CDCDFF&currStreakLabel=90A0D9&sideLabels=90A0D9&dates=CDCDFF"
      );
    } else {
      setSrc(
        "https://github-readme-streak-stats.herokuapp.com?user=vivekbtu&theme=tokyonight&hide_border=true&border_radius=20&background=FCFCFC&stroke=677F84&ring=2978B5&fire=677F84&currStreakNum=183C44&sideNums=183C44&currStreakLabel=405E65&sideLabels=405E65&dates=677F84"
      );
    }
  }, [themename]);
  //
  const [neww, setNew] = React.useState(
    themename === "light"
      ? "https://github-readme-stats.vercel.app/api?username=vivekbtu&theme=default&hide_border=true&include_all_commits=false&count_private=true"
      : "https://github-readme-stats.vercel.app/api?username=vivekbtu&theme=nord&hide_border=true&include_all_commits=false&count_private=true"
  );
  React.useEffect(() => {
    if (themename === "dark") {
      setNew(
        // "https://github-readme-stats.vercel.app/api?username=vivekbtu&theme=merko&hide_border=true&include_all_commits=false&count_private=true"
        "https://github-readme-stats-git-masterrstaa-rickstaa.vercel.app/api/top-langs/?username=vivekbtu&theme=nord&hide_border=true&include_all_commits=false&count_private=true"
      );
    } else {
      setNew(
        // "https://github-readme-stats.vercel.app/api?username=vivekbtu&theme=default&hide_border=true&include_all_commits=false&count_private=true"
        "https://github-readme-stats-git-masterrstaa-rickstaa.vercel.app/api/top-langs/?username=vivekbtu&theme=default&hide_border=true&include_all_commits=false&count_private=true"
      );
    }
  }, [themename]);

  ///

  const [stat, setStat] = React.useState(
    themename === "light"
      ? "https://github-readme-streak-stats.herokuapp.com?user=vivekbtu&theme=tokyonight&hide_border=true&border_radius=20&background=FCFCFC&stroke=677F84&ring=2978B5&fire=677F84&currStreakNum=183C44&sideNums=183C44&currStreakLabel=405E65&sideLabels=405E65&dates=677F84"
      : "https://github-readme-streak-stats.herokuapp.com?user=vivekbtu&theme=tokyonight&hide_border=true&border_radius=20&background=23283E&stroke=90A0D9&ring=90A0D9&fire=CDCDFF&currStreakNum=CDCDFF&sideNums=CDCDFF&currStreakLabel=90A0D9&sideLabels=90A0D9&dates=CDCDFF"
  );

  React.useEffect(() => {
    if (themename === "dark") {
      setStat(
        "https://github-readme-stats.vercel.app/api?username=vivekbtu&show_icons=true&theme=tokyonight&hide_border=true&border_radius=20&background=23283E&stroke=90A0D9&ring=90A0D9&fire=CDCDFF&currStreakNum=CDCDFF&sideNums=CDCDFF&currStreakLabel=90A0D9&sideLabels=90A0D9&dates=CDCDFF"
      );
    } else {
      setStat(
        "https://github-readme-stats.vercel.app/api?username=vivekbtu&show_icons=true&theme=tokyonight&hide_border=true&border_radius=20&background=FCFCFC&stroke=677F84&ring=2978B5&fire=677F84&currStreakNum=183C44&sideNums=183C44&currStreakLabel=405E65&sideLabels=405E65&dates=677F84"
      );
    }
  }, [themename]);

  const themes = {
    red: {
    //   title_color: "9ccfd8",
    //   icon_color: "ebbcba",
    //   text_color: "e0def4",
    //   bg_color: "191724",
    },
  };
  return (
    <>
      <div
        className="section main"
        //  data-aos="fade-right"
      >
        {/* <h2 className="section__title different"> </h2> */}

        <div
          className="section"
          style={{
            display: "flex",
            justifyContent: "center",
          }}
        >
          <div
            style={{
              width: "30%",
              margin: "auto",
            }}
          >
            <img
              id="github-top-langs"
              align="center"
              alt="error"
              // theme={themes}
              // style={{ bg_color: "373f51" }}
              src={neww}
              // src="htt/ps://github-readme-stats.vercel.app/api?username=vivekbtu"
              // height="30%"
              width="100%"
              style={{
                boxShadow: "var(--shadow)",
                borderRadius: "20px",
              }}
              height="100%"
            />
          </div>

          <div
            style={{
              width: "30%",
              margin: "auto",
            }}
          >
            <img
              id="github-streak-stats"
              align="center"
              // src="https://github-readme-streak-stats.herokuapp.com/?user=vivekbtu&theme=default&hide_border=true"
              src={src}
              alt="vivekbtu"
              // height="30%"
              width="100%"
              style={{ boxShadow: "var(--shadow)", borderRadius: "20px" }}
              height="100%"
            />
          </div>


          <div
            style={{
              width: "30%",
              margin: "auto",
            }}
          >
            <img
              id="github-stats-card"
              align="center"
              // src="https://github-readme-streak-stats.herokuapp.com/?user=vivekbtu&theme=default&hide_border=true"
              src={stat}
              alt="vivekbtu"
              // height="30%"
              width="100%"
              style={{ boxShadow: "var(--shadow)", borderRadius: "20px" }}
              height="100%"
            />
          </div>
        </div>
      </div>
    </>
  );
};
